;(function ($, APP) {
  APP.explosion = {}

  var canvas
  var ctx
  var colours = ['#41B8E6']

  var balls = []
  var multiplier = 0
  var width, height
  var origin
  var normal
  var power = 0.63
  var maxr = 30
  var reqID

  function init (_canvas) {
    if (!_canvas) return
    canvas = _canvas
    ctx = canvas.getContext('2d')

    canvas.width = window.innerWidth
    canvas.height = window.innerHeight

    width = canvas.width
    height = canvas.height

    origin = {
      x: width / 2,
      y: height / 2
    }
    normal = {
      x: width / 2,
      y: height / 2
    }
  }

  function Ball (x, y) {
    var that = this
    this.x = x
    this.y = y
    this.angle = Math.PI * 2 * Math.random()

    this.multiplier = randBetween(6, 12)

    this.vx = (this.multiplier + Math.random() * 0.5) * Math.cos(this.angle)
    this.vy = (this.multiplier + Math.random() * 0.5) * Math.sin(this.angle)
    this.r = randBetween(8, maxr) + 3 * Math.random()
    this.color = colours[Math.floor(Math.random() * colours.length)]
    this.direction = randBetween(-1, 1)

    this.update = function () {
      that.x += that.vx - normal.x
      that.y += that.vy - normal.y

      normal.x = -2 / width * Math.sin(that.angle)
      normal.y = -2 / height * Math.cos(that.angle)
    // normal.y = ((-2 / window.innerHeight) * Math.cos(this.angle)) + this.direction;

      that.r -= 0.8
      that.vx *= power
      that.vy *= power
    }
  }

  function pushBalls (count, x, y) {
    for (var i = 0; i < count; i++) {
      balls.push(new Ball(x, y))
    }
  }

  function randBetween (min, max) {
    return Math.floor(Math.random() * max) + min
  }

  function loop () {
    ctx.clearRect(0, 0, width, height)

    for (var i = 0; i < balls.length; i++) {
      var b = balls[i]

      if (b.r < 0) continue

      ctx.fillStyle = b.color
      ctx.beginPath()
      ctx.arc(b.x, b.y, b.r, 0, Math.PI * 2, false)
      ctx.fill()

      b.update()
    }

    multiplier -= 0.4

    removeBall()
    reqID = window.requestAnimationFrame(loop)
  }

  function removeBall () {
    for (var i = 0; i < balls.length; i++) {
      var b = balls[i]
      if (
      b.x + b.r < 0 ||
      b.x - b.r > width ||
      b.y + b.r < 0 ||
      b.y - b.r > height ||
      b.r < 0
    ) {
        balls.splice(i, 1)
      }
    }
  }

  function push (_x, _y, _p, _m) {
    maxr = _m || 30
    power = _p || 0.63
    var x = _x || width / 2
    var y = _y || height / 2
    pushBalls(randBetween(10, 20), x, y)
  }

  function pushSeq (_x, _y, _p, _m, _t) {
    if (!canvas) return
    resume()
    balls = []
    var cnt = 0
    var t = _t || 7
    console.log(t, _t)
    var intv = setInterval(function () {
      console.log('interv')
      push(_x, _y, _p, _m)
      cnt++
      if (cnt >= t) {
        clearInterval(intv)
        setTimeout(function () {
          pause()
        }, 3000)
      }
    }, 250)
  }

  function pause () {
    window.cancelAnimationFrame(reqID)
  }
  function resume () {
    loop()
  }

  APP.explosion.init = init
  APP.explosion.push = push
  APP.explosion.pushSeq = pushSeq
  APP.explosion.pause = pause
  APP.explosion.resume = resume

  $(document).ready(function () {
    if ($('#test')[0]) {
      APP.explosion.init($('#test')[0])
    }
  })
})(window.jQuery, window.APP)
