;(function ($, APP) {
  function _BonusState () {
    return {
      enter: function (option) {
        console.log('BonusState :: enter')
        APP.bonus.show()
        APP.audios.play('bubble_bonus')

        APP.bonus.setStartBonus(function () {
          APP.audios.play('bonus_speed_music', 2000)
        })

        APP.bonus.setEndBonus(function () {
          APP.audios.play('bubble_bonus')
          APP.audios.pause('bonus_speed_music')
        })

        APP.bonus.setClb(function () {
          setTimeout(function () {
            APP.stateman.go('find', {encode: false})
          }, 2000)
        })
      },
      leave: function (option) {
        console.log('BonusState :: leave')

        APP.game.addPoints(APP.bonus.getPoints())

        APP.bonus.hide()
      }
    }
  }
  window.BonusState = _BonusState
})(window.jQuery, window.APP)
