;(function ($, APP, firebase) {
  APP.fb = {}

  var list = []

  var config = {
    apiKey: 'AIzaSyCHyM7mOoccIeP6mz2t_8vQvutwcWoY6Rk',
    authDomain: 'backbone-54510.firebaseapp.com',
    databaseURL: 'https://backbone-54510.firebaseio.com',
    projectId: 'backbone-54510',
    storageBucket: 'backbone-54510.appspot.com',
    messagingSenderId: '993709166280'
  }
  firebase.initializeApp(config)

  var db = firebase.database()
  var ref = db.ref().child(window.fbroot)
  ref.on('value', function (snapshot) {
    var v = snapshot.val()
    list = []
    for (var k in v) {
      var o = v[k]
      o.id = k
      list.push(o)
    }
    list.sort(function (a, b) {
      return d3.descending(a.pt, b.pt)
    })
    console.log(list)
  })

  function add (name, pt, country) {
    var newkey = ref.push().key
    var ob = {}
    ob[newkey] = {
      name: name,
      pt: pt,
      country: country,
      created: new Date().toISOString()
    }
    ref.update(ob)
    console.log(newkey)
    return newkey
    // ref.push().set({
    //   name: name,
    //   pt: pt,
    //   created: new Date().toISOString()
    // })
  }

  function getList () {
    return list
  }

  function getPosition (pt) {
    var pos
    list.forEach(function (d, i) {
      if (!pos && pt >= d.pt) {
        pos = i + 1
      }
    })
    return pos || list.length
  }

  APP.fb.getPosition = getPosition
  APP.fb.getList = getList
  APP.fb.add = add
})(window.jQuery, window.APP, window.firebase)
