;(function ($, APP) {
  function _FindState () {
    return {
      enter: function (option) {
        console.log('FindState :: enter')

        $('#fume').transition({opacity: 1}, 500)

        APP.panel.show()
        APP.panel.setButton('play')
        APP.panel.setButtonLabel(APP.labels.splash_panel_button)
        APP.panel.setTarget(APP.game.getCurrent())
        APP.panel.updateNumber(APP.game.getItems(), APP.game.getTotal())

        var dato = APP.getDato()
        console.log(dato)

        APP.panel.setAction(dato.introduction_text)

        var vals = APP.game.getCurrentVal()
        APP.bar.setTime(vals.v, vals.p)

        var index = APP.game.getCurrent()
        APP.catch.preload(index)

        APP.back.restore()
      },
      leave: function (option) {
        console.log('FindState :: leave')

        APP.bar.show()
        APP.bar.setTarget(APP.game.getCurrent())
        APP.back.begin(APP.game.getCurrent())

        APP.panel.signalTarget()
        APP.panel.outBtn()
        APP.bar.signalTarget()

        APP.panel.hide(1000, 1500)

        APP.back.restore()

        // clone the target
        // get position of both

        $('#fume').transition({opacity: 0}, 500)
      }
    }
  }
  window.FindState = _FindState
})(window.jQuery, window.APP)
