;(function ($, APP, TweenMax) {
  APP.submit = {}

  $(document).ready(function () {
    var cont = $('#submit')
    var item = cont.find('.score_item')
    var input = cont.find('[js_text_name]')
    var _clb

    cont.find('[js_submit_btn]').on('click', function () {
      var name = input.val()
      _clb(name)
      return false
    })

    function show (delay) {
      var d = (delay) || 0
      cont.transition({y: 0, delay: d}, 1500, 'easeInOutQuint')
      input.val('')
      input.attr('placeholder', APP.labels.submit_tap_here)
      // cont.find('[js_text_name]').click()
    }

    function hide (time, delay) {
      var d = (delay) || 0
      var h = window.screen.height + 30
      var _t = (time > 0) ? time : 0
      cont.transition({y: h, delay: d}, _t, 'easeInOutQuint')

      item.removeClass('over')
      item.removeClass('won')
      cont.find('.img.won').css({opacity: 0})
      cont.find('.img.over').css({opacity: 0})
    }

    function showOver (ob) {
      item.removeClass('won')
      item.addClass('over')
      cont.find('[submit_title]').html(ob.submit_title)
      cont.find('[submit_level_label]').text(ob.submit_level_label)
      cont.find('.img.won').css({opacity: 0})
      cont.find('.img.over').css({opacity: 1})
      cont.find('.img.over.left img').css({x: '-30%', opacity: 0}).transition({x: 0, opacity: 1}, 1000, 'easeInOutQuint')
      cont.find('.img.over.right img').css({x: '30%', opacity: 0}).transition({x: 0, opacity: 1, delay: 150}, 1000, 'easeInOutQuint')
      cont.find('.head').removeClass('won').addClass('over')
      show()
    }

    function showWon (ob) {
      console.log('showWon', ob)
      item.removeClass('over')
      item.addClass('won')
      cont.find('[submit_title]').html(ob.submit_title)
      cont.find('[submit_level_label]').text(ob.submit_level_label)
      cont.find('.img.won').css({opacity: 1})
      cont.find('.img.over').css({opacity: 0})
      cont.find('.img.won.left img').css({x: '-30%', opacity: 0}).transition({x: 0, opacity: 1}, 1000, 'easeInOutQuint')
      cont.find('.img.won.right img').css({x: '30%', opacity: 0}).transition({x: 0, opacity: 1, delay: 150}, 1000, 'easeInOutQuint')
      cont.find('.head').removeClass('over').addClass('won')
      show()
    }

    function setPosition (p) {
      item.find('[js_your_position]').text(p + '.')
    }

    function setPoints (p) {
      item.find('[js_your_points]').text(p + 'pt')
    }

    function setFlag (c) {
      item.find('[js_your_flag] img').attr('src', 'assets/famfamfam_flag_icons/png/' + c + '.png')
    }

    function on (clb) {
      _clb = clb
    }

    APP.submit.on = on
    APP.submit.setPosition = setPosition
    APP.submit.setPoints = setPoints
    APP.submit.setFlag = setFlag
    APP.submit.showOver = showOver
    APP.submit.showWon = showWon
    APP.submit.show = show
    APP.submit.hide = hide

    hide()

    if (APP.version === undefined) showOver(500)
  })
})(window.jQuery, window.APP, window.TweenMax)
