;(function (APP, $) {
  var enabled = true

  var audios = {
    'bonus_speed_music': {loop: true, src: null, fadeout: true},
    'bonus_start': {loop: false, src: null},
    'bubble_bonus': {loop: false, src: null},
    'expired_time': {loop: false, src: null},
    'generic_click': {loop: false, src: null},
    'good_choice': {loop: false, src: null},
    'idle_music': {loop: true, src: null, fadeout: true},
    'latest_seconds': {loop: false, src: null},
    'play1': {loop: false, src: null},
    'play2': {loop: false, src: null},
    'scores_counter': {loop: false, src: null, fadeout: true},
    'wrong_choice': {loop: false, src: null},
    'soundscape': {loop: true, src: null, fadeout: true}
  }

  function play (k, delay) {
    console.log('play', k)
    var del = delay || 0
    setTimeout(function () {
      audios[k].src.volume = 1
      if (enabled) audios[k].src.play()
    }, del)
  }

  function pause (k) {
    console.log('pause', k)
    if (audios[k].fadeout) {
      TweenMax.to(audios[k].src, 2, {volume: 0,
        onComplete: function (p) {
          p.pause()
        },
        onCompleteParams: [audios[k].src]})
    } else {
      if (audios[k].src) audios[k].src.pause()
    }
  }

  function load () {
    for (var k in audios) {
      var el = audios[k]
      el.src = new Howl({src: ['assets/audio/' + k + '.mp3'], loop: el.loop})
    }

    $('[play]').on('click', function () {
      var t = $(this).attr('play')
      play(t)
    })
  }

  function setAudioEnabled (_status) {
    enabled = _status
    if (!enabled) {
      for (var k in audios) {
        pause(k)
      }
    }
  }

  APP.audios = {}
  APP.audios.load = load
  APP.audios.play = play
  APP.audios.pause = pause
  APP.audios.setAudioEnabled = setAudioEnabled
})(window.APP, window.jQuery)
