;(function ($, APP) {
  $(document).ready(function () {
    $('#notready').on('click', function () {
      window.location.href = 'http://formulae.enel.com/'
      return false
    })

    var widerFak = 1.7
    var currentFak
    $(window).on('resize', function () {
      var currentFak = $(window).width() / $(window).height()
      if (currentFak > widerFak) {
        $('html').addClass('wider')
      } else {
        $('html').removeClass('wider')
      }
    })
    $(window).trigger('resize')
  })
})(window.jQuery, window.APP)
