window.APP = {version: 'window.version', race: 'santiago', lang: 'eng'}

;(function ($, APP) {
  APP.game = new GamePlay()

  APP.getDato = function () {
    var dato = null
    var name = APP.game.getCurrentName()
    APP.db.forEach(function (d) {
      var n = d.layer_name.replace(/_\d_/g, '')
      console.log(n, name)
      if (n === name) dato = d
    })
    dato.description = dato['description_' + APP.lang]
    dato.introduction_text = dato['introduction_text_' + APP.lang]
    dato.label = dato['label_' + APP.lang]
    dato.you_found = dato['you_found_' + APP.lang]
    return dato
  }

  APP.setLanguage = function (lang) {
    APP.lang = lang
    APP.labels = {}
    var csv = APP.labels_src
    csv.forEach(function (d, i) {
      APP.labels[d.Label_id] = d['Text_' + lang]
    })

    for (var k in APP.labels) {
      var d = APP.labels[k]
      $('[' + k + ']').html(d)
    }
  }

  APP.country = 'us'
  $.get('http://ip-api.com/json', function (data) {
    console.log('ip-api.com', data)
    var cnt = data.countryCode
    APP.country = (cnt && cnt !== undefined) ? cnt.toLowerCase() : 'us'
  })

  $(document).ready(function () {
    $('[goref]').on('click', function () {
      var ob = {encode: false, param: {}}
      var ref = $(this).attr('goref').replace('/', '.')
      if (ref.indexOf('?') >= 0) {
        var parts = ref.split('?')
        ref = parts[0]
        var p = parts[1].split('=')
        ob.param[p[0]] = p[1]
      }
      APP.stateman.go(ref, ob)
    })

    $('svg').each(function (i, e) {
            // fix id name
            // sandbox ref id

      var elemns = $(e).find('[id]')
      var clips = $(e).find('[clip-path^="url"]')
      var fills = $(e).find('[fill^="url"]')
      var strokes = $(e).find('[stroke^="url"]')
      console.log(elemns.length, clips.length, clips.length, strokes.length)
    })
  })
})(window.jQuery, window.APP)
