;(function ($, APP) {
  function _WinState () {
    return {
      enter: function (option) {
        console.log('WinState :: enter')

        $('#fume').transition({opacity: 1}, 500)

        APP.game.abort()
        APP.game.addItem()
        APP.bar.hide()

      //    APP.panel.setAction( APP.labels.win_panel_label )
      //    APP.panel.setTitle( APP.labels.win_panel_title )
            // APP.panel.setTarget( -1 )
            // APP.panel.setPartial('won')
            // APP.panel.setButton('home')
            // APP.panel.setButtonLabel( APP.labels.win_panel_button )
            // APP.panel.updateNumber(APP.game.getItems(), APP.game.getTotal())
            // APP.panel.show()

        var dato = APP.getDato()
        console.log(dato)

        APP.catch.setTitle(dato.you_found)
        APP.catch.setText(dato.description)
        APP.catch.setAct(APP.labels.win_panel_title)
        APP.catch.setLabel(APP.labels.win_mainlabel, '#55BD5A')
        APP.catch.showTextOnly()
        APP.catch.setLink('submit/add?status=won')

        APP.audios.play('scores_counter')

        var index = APP.game.getCurrent()
        APP.catch.show(index)

        APP.explosion.init($('#catchExplo')[0])
        var pt = APP.catch.getCenter()
        setTimeout(function () {
          APP.explosion.pushSeq(pt.left, pt.top)
        }, 1000)

        APP.bar.setBarIcon(1)
        APP.bar.hide()
      },
      leave: function (option) {
        console.log('WinState :: leave')
        APP.catch.hide()
        $('#fume').transition({opacity: 0}, 500)
      }
    }
  }
  window.WinState = _WinState
})(window.jQuery, window.APP)
