;(function ($, APP) {
  function _LoadingState () {
    return {
      enter: function (option) {
        console.log('LoadingState :: enter')

        $(document).ready(function () {
          APP.audios.load()

          APP.langselector.setSelectClb(function (lang) {
            APP.setLanguage(lang)
          })

          APP.bar.setSoundClb(function (status) {
            console.log('setAudioEnabled', status)
            APP.audios.setAudioEnabled(status)
          })

          $.get('assets/labels.csv', function (data) {
            var csv = d3.csvParse(data)
            APP.labels_src = csv
            APP.setLanguage('eng')

            $.get('assets/data.csv', function (data) {
              var dbsrc = d3.csvParse(data)
              APP.db = dbsrc

              var dbsrc = d3.csvParse(data)
              if (APP.race && APP.race !== '') {
                var krace = APP.race
                APP.db = []
                dbsrc.forEach(function (d) {
                  if (d[krace] === 'x') APP.db.push(d)
                })
              }
              console.log(APP.db)

              APP.back.init()

              console.log(APP.back.elements)

              var actualTargets = []
              APP.back.elements.each(function (i, e) {
                var id = $(e).attr('id')
                APP.db.forEach(function (d) {
                  if (id === d.layer_name) actualTargets.push(e)
                })
              })

              console.log(actualTargets)
              APP.game.config(actualTargets)

              console.log('APP.stateman.current.name', APP.stateman.current.name)
              APP.stateman.go('home', {encode: false})
            })
          })
        })
      },
      leave: function (option) {
        console.log('LoadingState :: leave')

        $('#loading').hide()
      }
    }
  }
  window.LoadingState = _LoadingState
})(window.jQuery, window.APP)
