;(function ($, APP, TweenMax) {
  APP.panel = {}

  var isOpen = false

  $(document).ready(function () {
    var cont = $('#panel')
    var title = cont.find('[js_title]')
    var action = cont.find('[js_target_intro]')
    var cta = cont.find('.cta')
    var btn = cont.find('#btnPlay')
    var itms = cont.find('[js_items]')
    var targetsSvg = cont.find('.target')
    var currentTarget

    var lft = cont.find('.left img')
    var rgt = cont.find('.right img')

    targetsSvg.find('svg').attr('viewBox', '0 0 400 400')

    function show (delay) {
      isOpen = true
      var d = (delay) || 0
      cont.transition({y: 0, delay: d}, 1500, 'easeInOutQuint')
      lft.transition({opacity: 1, x: 0, delay: d + 900}, 1000, 'easeInOutQuint')
      rgt.transition({opacity: 1, x: 0, delay: d + 1050}, 1000, 'easeInOutQuint')
      cta.css({opacity: 1, y: 0})
    }

    function hide (time, delay) {
      isOpen = false
      var d = (delay) || 0
      var h = cont.height() + 30
      var _t = (time > 0) ? time : 0
      cont.transition({y: h, delay: d}, _t, 'easeInOutQuint')
      lft.transition({opacity: 0, x: -500, delay: d}, _t, 'easeInOutQuint')
      rgt.transition({opacity: 0, x: 500, delay: d}, _t, 'easeInOutQuint')
    }

    $(window).on('resize', function () {
      if (!isOpen) {
        var h = cont.height() + 30
        cont.css({y: h})
      }
    })

    function setTarget (id) {
      TweenMax.set(targetsSvg, {scale: 1, opacity: 1})

      var targets = targetsSvg.find('svg > g')

      console.log(id)

      targets.hide()

      if (id >= 0) {
        currentTarget = $(targets.get(id))
        currentTarget.show()

        // TweenMax.to(targetsSvg, 0.75, {scale: 0.9, repeat: -1, yoyo: true})
      }
    }

    function setTitle (v) {
      title.html(v)
    }

    function setAction (v) {
      console.log('setAction', v)
      action.text(v)
    }

    function setButton (url) {
      btn.attr('goref', url)
    }

    function setButtonLabel (v) {
      btn.text(v)
    }

    function setItemsNum (v) {
      itms.text(v)
    }

    function signalTarget () {
      TweenMax.killTweensOf(targetsSvg)
      TweenMax.set(targetsSvg, {scale: 1})
      TweenMax.to(targetsSvg, 1, {scale: 0.01, delay: 0.5, ease: Elastic.easeInOut})
    }

    function updateNumber (n, t) {
      var nn = (n < 10) ? '0' + n : n
      cont.find('[js_update_num]').text(nn + '/' + t)
    }

    function outBtn () {
      cta.transition({opacity: 0, y: 50}, 500, 'easeInOutQuint')
    }

    APP.panel.outBtn = outBtn
    APP.panel.updateNumber = updateNumber
    APP.panel.signalTarget = signalTarget
    APP.panel.setButtonLabel = setButtonLabel
    APP.panel.setItemsNum = setItemsNum
    APP.panel.setButton = setButton
    APP.panel.setAction = setAction
    APP.panel.setTitle = setTitle
    APP.panel.setTarget = setTarget
    APP.panel.show = show
    APP.panel.hide = hide

    hide()

    if (APP.version === undefined) show(500)
  })
})(window.jQuery, window.APP, window.TweenMax)
