;(function ($, APP) {
  function _CatchState () {
    var timer

    return {
      enter: function (option) {
        console.log('CatchState :: enter')

        var dato = APP.getDato()
        console.log(dato)

        APP.catch.setAct(APP.labels.nexttarget_label)
        APP.catch.setTitle(dato.you_found)
        APP.catch.setText(dato.description)
        APP.catch.setLabel(APP.labels.win_panel_label, '#55BD5A')

        APP.audios.play('scores_counter')
        APP.catch.setEndPoints(function () {
          APP.audios.pause('scores_counter')
        })

        var index = APP.game.getCurrent()
        APP.catch.show(index)

        APP.game.addItem()

        APP.bar.hide()
        APP.bar.setNum(APP.game.getItems())
        APP.bar.setBarIcon(1)
        var vals = APP.game.getCurrentVal()
        APP.bar.setTime(vals.v, vals.p)

        APP.explosion.init($('#catchExplo')[0])
        var pt = APP.catch.getCenter()
        setTimeout(function () {
          APP.explosion.pushSeq(pt.left, pt.top)
        }, 1000)

        APP.catch.setLink('find') // find
        if (APP.game.getLength() % 3 === 0 && APP.game.getLength() > 0 && APP.game.getLength() !== 12) {
          console.log(APP.game.getLength(), APP.game.getLength() % 3)
          APP.catch.setLink('bonus')
        }

        timer = setTimeout(function () {
          APP.back.pick(APP.game.getCurrent())
        }, 1000)
      },
      leave: function (option) {
        console.log('CatchState :: leave')
        APP.catch.hide()
        // APP.panel.signalTarget()

        // APP.panel.hide(1000, 500)
        APP.game.continue()

        APP.bar.setTarget(APP.game.getCurrent())
        APP.bar.setBarIcon(0)
        APP.bar.signalTarget()

        clearTimeout(timer)
      }
    }
  }
  window.CatchState = _CatchState
})(window.jQuery, window.APP)
