;(function ($, APP) {
  function _HomeState () {
    return {
      enter: function (option) {
        console.log('HomeState :: enter')

        $('.langs > div').on('click', function () {
          var lang = $(this).data('lang')
          console.log(lang)
          APP.setLanguage(lang)
          $('.langs > div').removeClass('current')
          $(this).addClass('current')
          // APP.langselector.show()
          return false
        })

        APP.game.reset()

        APP.bonus.level = 0

        $('#fume').transition({opacity: 1}, 500)

        APP.home.show()

        APP.bar.hide()
        var vals = APP.game.getCurrentVal()
        APP.bar.setTime(vals.v, vals.p)
        APP.bar.setNum(APP.game.getItems())
        APP.bar.setNumTot(APP.game.getTotal())

        APP.audios.play('idle_music', 2000)

        APP.back.restore()
      },
      leave: function (option) {
        console.log('HomeState :: leave')

        APP.audios.pause('idle_music')

        APP.home.hide()
        $('#fume').transition({opacity: 0}, 500)
      }
    }
  }
  window.HomeState = _HomeState
})(window.jQuery, window.APP)
