;(function ($, APP, TweenMax) {
  APP.langselector = {}

  var isOpen = false
  var onSelectClb

  $(document).ready(function () {
    var cont = $('#langselector')

    cont.find('li').on('click', function () {
      var lang = $(this).data('lang')
      hide(350)
      onSelectClb(lang)
    })

    function show (delay) {
      isOpen = true
      var d = (delay) || 0
      cont.transition({y: 0, delay: d}, 1500, 'easeInOutQuint')
    }

    function hide (time, delay) {
      isOpen = false
      var d = (delay) || 0
      var h = cont.height() + 30
      var _t = (time > 0) ? time : 0
      cont.transition({y: h, delay: d}, _t, 'easeInOutQuint')
    }

    $(window).on('resize', function () {
      if (!isOpen) {
        var h = cont.height() + 30
        cont.css({y: h})
      }
    })

    function setSelectClb (clb) {
      onSelectClb = clb
    }

    APP.langselector.setSelectClb = setSelectClb
    APP.langselector.show = show
    APP.langselector.hide = hide

    hide()
    cont.css('opacity', 1)

    if (APP.version === undefined) show(500)
  })
})(window.jQuery, window.APP, window.TweenMax)
