;(function ($, APP) {
  APP.bonus = {level: 0}

  $(document).ready(function () {
    var back = $('#bonusback')
    var front = $('#bonusfront')
    var left = front.find('.left > div')
    var right = front.find('.right > div')
    var panel = $('#bonuspanel')
    var fume = $('#bonusfume')
    var push = $('#pushBtn')
    var disc = $('#bnsball').css({scale: 0, opacity: 0})
    var bnstxt = $('#bonusBall .txt h1').css({scale: 0, opacity: 0})
    var bonusPoints = fume.find('[js_bonus_points]')
    var startBtn = panel.find('[js_start_bonus]')

    var finalClb
    var startBonusClb
    var endBonusClb

    $(window).on('resize', function () {
      var _w = $('body').width()
      var _b = back.width()
      var _d = (_b - _w) / 2
      back.css({x: _d * -1})
    })

    function pushup () {
      APP.colonn.pushup()
      push.find('.btn_bgd').removeClass('anim')
      TweenMax.fromTo(push, 0.15, {scale: 0.9}, {scale: 1})
      return false
    }

    function show () {
      $(window).trigger('resize')
      fume.css({opacity: 0})
      push.transition({opacity: 0, scale: 0.1}, 1000, 'easeInOutExpo')
      push.addClass('bypass')
      push.find('.btn_bgd').addClass('anim')
      APP.colonn.setClbEnd(showEnd)

      disc.css({scale: 0, opacity: 1})
      disc.transition({scale: 3, transformOrigin: '50% 50%'}, 500, 'easeInOutQuint')

      APP.explosion.init($('#bonusExplo')[0])
      setTimeout(function () {
        APP.explosion.pushSeq(null, null, 0.96, 60)
      })

      bnstxt.css({scale: 0, opacity: 1})
      bnstxt.transition({scale: 1,
        opacity: 1,
        delay: 1000,
        transformOrigin: '50% 50%',
        complete: function () {
          setTimeout(function () {
            postShow()
          }, 2000)
        }}, 1000, 'easeInOutQuint')
    }

    function postShow () {
      disc.transition({opacity: 0}, 1000)
      bnstxt.transition({opacity: 0}, 500)

      back.css({opacity: 1})
      front.css({opacity: 1})

      APP.colonn.reset()

      panel.transition({y: 0, delay: 500}, 1250, 'easeInOutExpo')

      startBtn.on('click', function () {
        startBtn.off()
        prepare()
        return false
      })
    }

    function hide () {
      panel.transition({y: '100%'}, 500)

      back.transition({opacity: 0}, 500)

      front.transition({opacity: 0}, 500)
      left.transition({x: -300, opacity: 0}, 500)
      right.transition({x: 300, opacity: 0}, 500)

      fume.transition({opacity: 0}, 500)
      push.transition({opacity: 0, scale: 0.1}, 1000, 'easeInOutExpo')
    }

    function hidePanel () {
      panel.transition({y: '100%'}, 500, 'easeInOutExpo')
    }

    function prepare () {
      hidePanel()
      left.transition({x: 0, opacity: 1}, 1500, 'easeInOutExpo')
      right.transition({x: 0, opacity: 1}, 1500, 'easeInOutExpo')

      APP.colonn.prepare()

      push.transition({opacity: 1, scale: 1, delay: 500}, 1500, 'easeInOutExpo')
      push.removeClass('bypass')

      push.on('click', pushup)

      startBonusClb()

      setTimeout(function () {
        APP.colonn.start(APP.bonus.level)
        APP.bonus.level++
      }, 2000)
    }

    function showEnd (won) {
      console.log('showEnd')

      endBonusClb()

      push.off('click')

      fume.transition({opacity: 1}, 500)
      push.transition({opacity: 0, scale: 0.1}, 1000, 'easeInOutExpo')

      bonusPoints.text('+0')

      APP.explosion.init($('#bonusExplo')[0])
      APP.explosion.pushSeq(null, null, 0.96, 60, 12)

      var pt = 0
      var dt = APP.colonn.getData()
      var maxpt = dt.you
      if (dt.you > dt.cpu) maxpt += dt.you - dt.cpu

      var ob = {v: 0}
      bonusPoints.text('+' + pt)
      TweenMax.to(ob, 3, {v: maxpt,
        onUpdate: function () {
          bonusPoints.text('+' + parseInt(ob.v))
        },
        onComplete: function () {
          finalClb()
        }
      })
    }

    function getPoints () {
      return APP.colonn.getData().you
    }

    function setClb (clb) {
      finalClb = clb
    }

    function setStartBonus (clb) {
      startBonusClb = clb
    }

    function setEndBonus (clb) {
      endBonusClb = clb
    }

    APP.bonus.setClb = setClb
    APP.bonus.setStartBonus = setStartBonus
    APP.bonus.setEndBonus = setEndBonus
    APP.bonus.getPoints = getPoints
    APP.bonus.hide = hide
    APP.bonus.show = show

    hide()
    if (APP.version === undefined) setTimeout(show, 500)
  })
})(window.jQuery, window.APP)
