;(function ($, APP, TweenMax) {
  APP.colonn = {}

  $(document).ready(function () {
    var greenRect = $('#green-rect')
    var greenArr = $('#green-arrow')
    var greenTri = $('#green-triangle')

    var bluRect = $('#blu-rect')
    var bluArr = $('#blu-arrow')
    var bluTri = $('#blu-triangle')

    var interv
    var _clb
    var _idletimer

    var points = {cpu: 0, you: 0}

    var initConfig = {gy: +greenRect.attr('y'),
      gh: +greenRect.attr('height'),
      by: +bluRect.attr('y'),
      bh: +bluRect.attr('height')
    }

    function reset () {
      TweenMax.to(greenRect, 1, {attr: {height: initConfig.gh, y: initConfig.gy}, ease: Expo.easeInOut})
      TweenMax.to(greenArr, 1, {y: 0, ease: Expo.easeInOut})
      TweenMax.to(greenTri, 1, {y: 0, ease: Expo.easeInOut})

      TweenMax.to(bluRect, 1, {delay: 0.15, attr: {height: initConfig.bh, y: initConfig.by}, ease: Expo.easeInOut})
      TweenMax.to(bluArr, 1, {delay: 0.15, y: 0, ease: Expo.easeInOut})
      TweenMax.to(bluTri, 1, {delay: 0.15, y: 0, ease: Expo.easeInOut})

      points = {cpu: 0, you: 0}

      clearInterval(interv)
    }

    function prepare () {
      TweenMax.to(greenRect, 1, {attr: {height: 0, y: initConfig.gy + initConfig.gh}, ease: Expo.easeInOut})
      TweenMax.to(greenArr, 1, {y: initConfig.gh, ease: Expo.easeInOut})
      TweenMax.to(greenTri, 1, {y: initConfig.gh, ease: Expo.easeInOut})

      TweenMax.to(bluRect, 1, {delay: 0.15, attr: {height: 0, y: initConfig.by + initConfig.bh}, ease: Expo.easeInOut})
      TweenMax.to(bluArr, 1, {delay: 0.15, y: initConfig.bh, ease: Expo.easeInOut})
      TweenMax.to(bluTri, 1, {delay: 0.15, y: initConfig.bh, ease: Expo.easeInOut})
    }

    function endgame () {
      clearInterval(interv)
      clearInterval(_idletimer)
      _clb()
    }

    function start (level) {
      var timer = [350, 250, 150, 75]
      interv = setInterval(function () {
        points.cpu += 5
        editBarCpu()
        if (points.cpu >= 100) {
          endgame()
        }
      }, timer[level])
    }

    function pushup () {
      resetIdle()
      points.you += 5

      var h = initConfig.bh / 100.0 * Math.min(100, points.you)
      var y = initConfig.by + initConfig.gh - h

      TweenMax.set(bluRect, {attr: {fill: '#ffffff'}})
      TweenMax.set(bluTri, {attr: {fill: '#ffffff'}})
      TweenMax.to(bluRect, 0.5, {attr: {height: h, y: y, fill: '#41B9E6'}, overwrite: true})
      TweenMax.to(bluArr, 0.5, {y: initConfig.bh - h, overwrite: true})
      TweenMax.to(bluTri, 0.5, {y: initConfig.bh - h, attr: {fill: '#41B9E6'}, overwrite: true})

      if (points.you >= 100) {
        console.log(points)
        endgame()
      }
    }

    function resetIdle () {
      clearInterval(_idletimer)
      _idletimer = setInterval(function () {
        points.you = (points.you > 0) ? points.you - 5 : 0
        var h = initConfig.bh / 100.0 * Math.min(100, points.you)
        var y = initConfig.by + initConfig.gh - h
        TweenMax.to(bluRect, 0.5, {attr: {height: h, y: y}, overwrite: true, ease: Linear.easeNone})
        TweenMax.to(bluArr, 0.5, {y: initConfig.bh - h, overwrite: true, ease: Linear.easeNone})
        TweenMax.to(bluTri, 0.5, {y: initConfig.bh - h, overwrite: true, ease: Linear.easeNone})
      }, 510)
    }

    function editBarCpu () {
      var h = initConfig.gh / 100.0 * Math.min(100, points.cpu)
      var y = initConfig.gy + initConfig.gh - h

      TweenMax.to(greenRect, 0.5, {attr: {height: h, y: y}})
      TweenMax.to(greenArr, 0.5, {y: initConfig.gh - h})
      TweenMax.to(greenTri, 0.5, {y: initConfig.gh - h})
    }

    function setClbEnd (clb) {
      _clb = clb
    }

    function getData () {
      return points
    }

    APP.colonn.getData = getData
    APP.colonn.start = start
    APP.colonn.setClbEnd = setClbEnd
    APP.colonn.pushup = pushup
    APP.colonn.prepare = prepare
    APP.colonn.reset = reset

    if (APP.version === undefined) reset()
  })
})(window.jQuery, window.APP, window.TweenMax)
