;(function ($, APP) {
  APP.catch = {}

  $(document).ready(function () {
    console.log('ready')

    var cont = $('#catch')

    var act = cont.find('.wrapper span')
    var title = cont.find('.wrapper h1')
    var desc = cont.find('.wrapper p')
    var label = cont.find('.header h2')
    var cta = cont.find('.cta')
    var next = cont.find('.cta .btn')
    var points = cont.find('[js_num_points]')
    var logo = cont.find('.top')
    var fire = cont.find('.fireworks')

    var header = cont.find('.header').css({opacity: 0})

    var imgFound = cont.find('#imgFound').css({opacity: 0})
    var disc = cont.find('#ball').css({scale: 0, opacity: 0})

    var endPoints

        // var ball = svgFound.find('#ball').css({opacity:0, scale: 0, transformOrigin: '50% 50%'})

    var p = cont.find('.middle .wrapper > *').css({opacity: 0})

    $(window).on('resize', function () {
      var __h = $(window).height()
      imgFound.css('height', __h)
    })

    function preload (item) {
      var path = getImgPath(item)
      var img = new Image()
      img.src = path
    }

    function getImgPath (item) {
      var num = (item < 9) ? '0' + (item + 1) : item + 1
      return 'assets/you_found_jpg/you_found_' + num + '.jpg'
    }

    function show (item) {
      cont.show()

      var scale = ($('html').hasClass('portrait')) ? 2 : 3.5

      disc.css({scale: 0, opacity: 1})

      // console.log('item', item)
      if (item !== undefined) imgFound.find('img').attr('src', getImgPath(item))
      imgFound.css({opacity: 0})

      var __w = $(window).width()
      var __h = $(window).height()
      imgFound.css('height', __h)
      // var widthContent = imgFound.width()
      // imgFound.css({x: (widthContent - widthView) / 2 * -1})

      disc.transition({scale: scale,
        transformOrigin: '50% 50%',
        complete: function () {
          showTextOnly()

          imgFound.css({opacity: 1})
          header.transition({opacity: 1}, 500)
          logo.transition({opacity: 1}, 500)
          cta.transition({opacity: 1}, 500)

          disc.transition({opacity: 0, delay: 200}, 1000, 'easeInOutQuart')
        }}, 750, 'easeInOutQuint')
    }

    function showTextOnly () {
      p.each(function (i, e) {
        $(this).transition({opacity: 1, delay: i * 200 + 500}, 1000)
      })
      cont.removeClass('bypass')
    }

    function hide () {
      p.each(function (i, e) {
        $(this).transition({opacity: 0}, 1000)
      })
      cont.addClass('bypass')

      imgFound.transition({opacity: 0}, 500)
      header.transition({opacity: 0}, 500)
      logo.transition({opacity: 0}, 500)
      cta.transition({opacity: 0}, 500)

      setTimeout(function () {
        cont.hide()
        imgFound.find('img').attr('src', null)
      }, 1000)
    }

    function setTitle (t) {
      title.text(t)
    }

    function setText (t) {
      desc.text(t)
    }

    function setAct (t) {
      act.text(t)
    }

    function setLabel (t, c) {
      label.text(t)
      label.css('color', c)
    }

    function setLink (l) {
      next.attr('goref', l)
    }

    function setPoints (p) {
      var ob = {v: 0}
      points.text(0)
      TweenMax.to(ob, 2, {delay: 1,
        v: p,
        onUpdate: function () {
          points.text(parseInt(ob.v))
        },
        onComplete: function () {
          if (endPoints) endPoints()
        }
      })
    }

    function getCenter () {
      var w = fire.outerWidth()
      var h = fire.outerHeight()
      var ob = fire.offset()
      ob.top += h / 2
      ob.left += w / 2
      return ob
    }

    function setEndPoints (clb) {
      endPoints = clb
    }

    APP.catch.preload = preload
    APP.catch.setEndPoints = setEndPoints
    APP.catch.getCenter = getCenter
    APP.catch.showTextOnly = showTextOnly
    APP.catch.setPoints = setPoints
    APP.catch.setTitle = setTitle
    APP.catch.setAct = setAct
    APP.catch.setText = setText
    APP.catch.setLabel = setLabel
    APP.catch.setLink = setLink
    APP.catch.hide = hide
    APP.catch.show = show

    hide()
    if (APP.version === undefined) {
      setTimeout(function () {
        show(0)
      }, 1500)
    }
  })
})(window.jQuery, window.APP)
