;(function ($, APP) {
  APP.back = {}

  function init () {
    console.log('init back')
    var wrap = $('#backwrap')
    var cont = $('#back')
    var front = $('#front')
    var frontSvg = front.find('svg')
    var svg = $('#back svg')

    var bandl = $('.bandleft')
    var bandr = $('.bandright')

    var impe
    var frontFak = 1.64
    var isZoomed = false
    var fakZoom = 2
    var baseFrontX

    var mc = new Hammer.Manager(document.getElementById('backwrap'))
    mc.add(new Hammer.Tap({ event: 'doubletap', taps: 2 }))
    mc.on('singletap doubletap', function (ev) {
      if (isZoomed) {
        zoomOut()
      } else {
        zoomIn()
      }
    })

    var onClicked

    var elements = svg.find('#uomini > image')

    elements.on('click', function (e) {
      onClicked()
    })

    var widthView = $('body').width()
    var heightView = $('body').height()
    var widthContent = cont.width()
    var heightContent = cont.height()
    var bound

    impe = new Impetus({
      source: '#back',
      boundY: [heightContent / 4 * -1, heightContent / 4],
      bounce: true,
      // initialValues: [0, 0],
      update: function (x, y) {
        var ob = {x: x, y: y}
        if (!isZoomed) ob.y = 0
        cont.css(ob)

        ob.x = baseFrontX + (baseFrontX - x) * frontFak * -1
        front.css(ob)
      }
    })

    $(window).on('resize', function () {
      restore()
    })

    function restore () {
      wrap.removeClass('anim')
      if (isZoomed) zoomOut()
      console.log('restore')
      widthView = $('body').width()
      heightView = $('body').height()
      var ratio = widthView / heightView

      widthContent = cont.width()
      heightContent = cont.height()
      var perc = heightContent / 1080

      var band = (widthContent * perc - widthView) / 2

      if (ratio >= 1.77777777777) {
        bound = 1
        console.log('BAND')
        // show band
      } else {
        bound = band
      }

      var bandSize = band * -1
      console.log(widthView, widthContent * perc - widthView)

      bandl.width(bandSize)
      bandr.width(bandSize)

      var x = (widthContent - widthView) / 2 * -1
      var x2 = (frontSvg.width() - widthView) / 2 * -1
      frontSvg.css({x: (x2 - x)})

      var y = (heightContent - heightView) / 2 * -1

      baseFrontX = x

      cont.css({x: x})
      front.css({x: x})
      impe.setValues(x, y)
      impe.setBoundX([x - bound, x + bound])
      impe.setBoundY([heightContent / 4 * -1, heightContent / 4])

      wrap.addClass('anim')
    }

    function begin (picked) {
      // impe.pause()
      elements.css('display', 'none')
      $(elements.get(picked)).css('display', 'block')
    }

    function pick (picked) {
      elements.css('display', 'none')
      $(elements.get(picked)).css('display', 'block')
    }

    function show () {
      cont.show()
    }

    function hide () {
      cont.hide()
    }

    function onClickedClb (clb) {
      onClicked = clb
    }

    function zoomIn () {
      wrap.css({scale: 2})
      isZoomed = true

      var x = (widthContent - widthView) / 2 * -1
      impe.setBoundX([x - bound * 2, x + bound * 2])
    }

    function zoomOut () {
      isZoomed = false
      wrap.css({scale: 1})
      impe.setBoundY([-1, 1])
      var x = (widthContent - widthView) / 2 * -1
      impe.setBoundX([x - bound, x + bound])
      setTimeout(restore, 750)
    }

    APP.back.onClicked = onClickedClb
    APP.back.elements = elements
    APP.back.restore = restore
    APP.back.begin = begin
    APP.back.pick = pick
    APP.back.hide = hide
    APP.back.show = show
    APP.back.zoomIn = zoomIn
    APP.back.zoomOut = zoomOut
  }

  APP.back.init = init

  $(document).ready(function () {
    if (APP.version === undefined && $('#back')[0]) {
      setTimeout(function () {
        init()
        APP.audios.load()
        // $('body').on('click', function () {
        //   // APP.audios.play('generic_click')
        // })
        setTimeout(function () {
          APP.audios.play('play2')
        }, 5000)
      }, 500)
    }
  })
})(window.jQuery, window.APP)
