;(function ($, APP, StateMan) {
  'use strict'

  var frag = window.location.pathname

  if (window.location && window.location.hash.indexOf('#') >= 0) {
    window.location.href = frag
  }

  var submitState = new SubmitState()

  $(document).ready(function () {
    APP.stateman = new StateMan()
    APP.stateman.state({

      'loading': new LoadingState(),
      'home': new HomeState(),
      'find': new FindState(),
      'play': new PlayState(),
      'play.catch': new CatchState(),
      'play.over': new OverState(),
      'play.win': new WinState(),

      'bonus': new BonusState(),
      'submit': submitState,
      'submit.add': submitState.add,
      'submit.list': submitState.list

    }).on('notfound', function () {
      setTimeout(function () {
        APP.stateman.go('loading', {encode: false})
      }, 250)
    }).start({html5: false})
  })
})(window.jQuery, window.APP, window.StateMan)
