;(function ($, APP, TweenMax) {
  APP.board = {}

  var isOpen = false

  $(document).ready(function () {
    var cont = $('#board')
    var list = cont.find('.list')
    var template = list.find('#scoreItemTemplate').removeAttr('id').detach()
    var selected

    console.log(template)

    function show (delay) {
      isOpen = true
      var d = (delay) || 0
      cont.transition({y: 0, delay: d}, 1500, 'easeInOutQuint')
      $('.list').animate({ scrollTop: 0 }, 0)

      setTimeout(function () {
        if (selected) {
          var h = list.height()
          var p = selected.offset().top
          var d = p - h
          console.log(p)
          if (d > 0) {
            $('.list').animate({ scrollTop: p - 120 }, 750)// .scrollTop(p - 120)
          }
        }
      }, 1600)
    }

    function hide (time, delay) {
      isOpen = false
      var d = (delay) || 0
      var h = cont.height() + 30
      var _t = (time > 0) ? time : 0
      cont.transition({y: h, delay: d}, _t, 'easeInOutQuint')
    }

    $(window).on('resize', function () {
      if (!isOpen) {
        var h = cont.height() + 30
        cont.css({y: h})
      }
    })

    function updateList (arr, cls, currentID) {
      list.empty()
      arr.forEach(function (d, i) {
        var item = template.clone()
        item.find('.position').text(i + 1)
        item.find('.flag img').attr('src', 'assets/famfamfam_flag_icons/png/' + d.country + '.png')
        item.find('.name input').val(d.name)
        item.find('.points').text(d.pt + 'pt')
        if (d.id === currentID) {
          item.addClass(cls)
          selected = item
        }
        list.append(item)
      })
    }

    // scoreItemTemplate
    APP.board.updateList = updateList
    APP.board.show = show
    APP.board.hide = hide

    hide()

    if (APP.version === undefined && $('#board')[0]) {
      var arr = []
      for (var i = 0; i < 50; ++i) {
        arr.push({id: i, name: 'a' + i, pt: 100, country: 'it'})
      }
      var rnd = parseInt(Math.random() * arr.length)
      updateList(arr, 'won', rnd)
      show()
    }
  })
})(window.jQuery, window.APP, window.TweenMax)
