;(function ($, APP) {
  function _PlayState () {
    return {
      enter: function (option) {
        console.log('PlayState :: enter')

        APP.audios.play('soundscape')

        setTimeout(function () {
          APP.game.start()
        }, 1500)

        APP.game.onTick(function (v, p) {
          APP.bar.setTime(v, p)
        })

        APP.game.onOver(function () {
          APP.catch.setPoints(APP.game.getPoints())
          APP.stateman.go('play.over', {encode: false})
        })

        APP.bar.setLastTimeClb(function () {
          APP.audios.play('latest_seconds')
        })

        APP.back.onClicked(function () {
          if (APP.game.over) return // enforcing concurrency

          APP.game.addPoints()
          APP.catch.setPoints(APP.game.getPoints())

          if (APP.game.getLength() === 0) {
            APP.stateman.go('play.win', {encode: false})
          } else {
            APP.stateman.go('play.catch', {encode: false})
            APP.audios.play('good_choice')
          }
        })
      },
      leave: function (option) {
        console.log('PlayState :: leave')
        APP.audios.pause('soundscape')
        APP.game.abort()
      }
    }
  }
  window.PlayState = _PlayState
})(window.jQuery, window.APP)
