;(function ($, APP) {
  var fakenames = [
    'ElectroGuru',
    'Flash',
    'Spark',
    'FlowerPower',
    'GreenGo',
    'SmartFan',
    'NRG4U',
    'EnergyNinja'
  ]

  function _SubmitState () {
    return {
      enter: function (option) {
        console.log('SubmitState :: enter')
        $('#fume').transition({opacity: 1}, 500)
      },
      leave: function (option) {
        console.log('SubmitState :: leave')
        $('#fume').transition({opacity: 0}, 500)
      },

      add: {
        enter: function (option) {
          console.log('SubmitState add :: enter', option)
          var num = APP.game.getItems() - 1

          var ob = {
            submit_level_label: (option.param.status === 'won') ? APP.labels.win_pre_title : APP.labels.submit_pre_title_over,
            submit_title: (option.param.status === 'won') ? APP.labels.win_title : APP.labels.submit_title_over
          }
          if (option.param.status === 'over') {
            ob.submit_title = ob.submit_title.replace('XX', num)
            APP.submit.showOver(ob)
          }
          if (option.param.status === 'won') APP.submit.showWon(ob)

          var pt = APP.game.getPoints()
          APP.submit.setPoints(pt)
          APP.submit.setFlag(APP.country)
          APP.submit.setPosition(APP.fb.getPosition(pt))

          APP.submit.on(function (name) {
            var nm = name
            if (nm === '') {
              var pick = parseInt(Math.random() * fakenames.length)
              var newname = fakenames[pick]
              nm = newname + parseInt(Math.random() * 1000)
            }
            var id = APP.fb.add(nm, pt, APP.country)
            option.param.id = id
            option.encode = false
            APP.stateman.go('submit.list', option)
          })
        },
        leave: function (option) {
          console.log('SubmitState add :: leave', option)
          APP.submit.hide(1250)
        }
      },

      list: {
        enter: function (option) {
          console.log('SubmitState list :: enter', option)
          var status = option.param.status
          var id = option.param.id

          APP.board.updateList(APP.fb.getList(), status, id)
          APP.board.show()
        },
        leave: function (option) {
          console.log('SubmitState list :: leave')
          APP.board.hide(500)
        }
      }
    }
  }
  window.SubmitState = _SubmitState
})(window.jQuery, window.APP)
