;(function ($, APP) {
  $(document).ready(function () {
    var loc = window.location.pathname.replace('index.html', '')

    if (APP.stateman.current) {
      var v = loc + APP.stateman.current.name
      console.log('init')
      if (window.ga) {
        ga('set', 'page', v)
        ga('send', 'pageview', v)
      }
    }

    APP.stateman.on('begin', function (e) {
      var v = loc + APP.stateman.current.name
      console.log('begin', v)
      if (window.ga) {
        ga('set', 'page', v)
        ga('send', 'pageview', v)
      }
    })

    $('[track]').on('click', function () {
      var t = $(this).attr('track')
      var parts = t.split('__')
      console.log(loc + parts[0], parts[1])
      if (window.ga) {
        ga('send', 'event', loc + parts[0], parts[1])
      }
    })
  })
})(window.jQuery, window.APP)
